@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca&family=Montserrat:ital,wght@1,900&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.container-fluid{
    background: url(../img/sunseth.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}

.colorOverly{
    width: 100%;
	height: 100vh;
	background-color: #00000078;
	position: absolute;
}


h1{
    font-family: 'Montserrat', sans-serif;
    font-size: 4.5rem;
    color: #fff;
    text-shadow: 1px 1px 10px black;
}

h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 3rem;
    color: #fff;
    text-shadow: 1px 1px 10px black;
}

.weather-wrapper{
    width: 100%;
    height: 100vh;
    position: relative;
}

.weather-wrapper:before{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.weather-app{
    max-width: 700px;
    margin: auto;
    padding: 0 1rem;
    position: relative;
    top: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 1;
}

.location{
    text-align: center;
}

.location input{
    background-color: rgba(235, 235, 235, 0.592);
    padding: 1rem;
    border-radius: 10px;
    border: none;
    font-family: 'Lexend Deca', sans-serif;
    color: #262626;
    width: 350px;
    font-size: 1.1rem;
    text-transform: capitalize;
    outline: none;
}

.location input::placeholder{
    color: #262626b2;
    text-transform: uppercase;
    font-weight: 300;
}

.temp{
    text-align: center;
}

.location .loc-name{
    margin-top: 100px;
}

.weather-wrapper .description{
    position: relative;
    right: -90%;
    transform-origin:  0 0;
    transform: rotate(270deg);
    font-family: 'Lexend Deca', sans-serif;
    
}

.weather-wrapper .description p{
    color: #c0c0c0;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: 1rem;
}

.weather-wrapper .bottom{
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 17px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    font-family: 'Lexend Deca', sans-serif;
}

.footer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer p{
    color: rgb(204, 204, 204);
    font-size: 14px;
    font-family: 'Lexend Deca', sans-serif;
}

.footer span{
    color: red;
}